import { Card, CardBody } from "react-bootstrap";
import React from "react";
import { reArrangeSeries } from "../../../../../utils";
import SectionHeading from "../../layout/common/sectionHeading";
const AssetsTypeLineChart = React.lazy(() =>
  import("../../layout/common/graph/assetsTypeLineChart")
);
const ContentAnalytics = (props) => {

  const { contentAnalytics, loader } = props

  const contentAnalyticsData =
    contentAnalytics &&
    Object.keys(contentAnalytics).length &&
    reArrangeSeries(contentAnalytics, 1,1);

  return (
    <Card className="sw-card-custom gutter-b p-5 card-shadow mb-5">
      <SectionHeading headingMargin={1} section={3}  title={'Content Analytics'} />
      <CardBody className="px-0 pb-0">
          <AssetsTypeLineChart
            section={1}
            key={loader}
            contentAnalyticsData={loader ? null : contentAnalyticsData}
            type={1}
          />
      </CardBody>
    </Card>
  );
};

export default React.memo(ContentAnalytics);
